@use "../../App.scss" as *;

.button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(255, 255, 255);
    box-shadow: 1px 1px 14px 4px #b1b1b136;
    border-radius: 50%;
}

.button:hover{
    box-shadow: unset;
}